import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import styles from './header.module.css'

const Header = ({ siteTitle, interlinear, version }) => (
  <header className={styles.header}>
    <div className={styles.topNav}>
      <Link
        to="/"
        style={{
          color: `white`,
          textDecoration: `none`,
        }}
      >
        {siteTitle}
      </Link>
      {(version === 'GNA') ? (
        <div className={styles.interlinearIcon} onClick={interlinear}>
          <i class="pi pi-info-circle" style={{fontSize: '2em', color: 'white'}}></i>
        </div>
      ) : null}
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
